/* StudentLogin.css */

.student-container {
    padding: 20px;
    width: 80%;
  }
  
  .student-container .card {
    padding: 0px 15px;
    border-radius: 20px;
  }
  
  .student-container a {
    margin: 0px;
    font-size: 13px;
    border-radius: 7px;
    text-decoration: none;
    color: black;
  }
  
  .student-c1 {
    background-color: #fff2f1;
    border-radius: 20px;
  }
  
  .student-c2 {
    /* Add styles for the second column if needed */
  }
  
  .student-container form {
    margin-top: 70px;
    width: 100%; /* Decrease form size to 50% */
  }
  
  .student-container form > * {
    margin: 10px 0px;
  }
  
  .student-container #forgot {
    margin: 0px -60px;
  }
  
  .student-container #register {
    text-align: center;
  }
  
  .student-container img {
    background-color: antiquewhite;
  }
  
  .student-container .wlcm {
    font-size: 30px;
  }
  
  .student-container .sp1 {
    font-size: 5px;
  }
  
  .student-container .sp1 > span {
    background-color: #f0c3be;
  }
  
  .student-container input,
  .student-container button {
    width: 100%; /* Adjusted width to 100% within the form */
    background-color: #fff2f1;
    border-radius: 8px;
    padding: 8px 17px;
    font-size: 13px;
    border: 1px solid #f5f0ef;
    color: black;
  }
  
  .student-container input::placeholder {
    color: black; /* Set placeholder color to black */
  }
  
  .student-container .bt {
    background-color: #ff4133;
    border: 1px solid rgb(300, 200, 200);
  }
  